import React, { Component } from "react";
import Input from "./Input";

class Form extends Component {
  // Abstrakt component, har ingen render()

  // Statet heter samma bde hr och i formulret under.
  // D skrivs statet ver med infon i formulret under.
  state = {
    data: {},
    errors: {},
  };

  // Tar in alla fel i formulret och sparar i error.
  // Anvnder Joi-schemat i formulret under.
  // Om error r tom returneras inget.
  // Annars, lopar ver errors och sparar errormeddelandet under rtt property.
  validate() {
    const options = { abortEarly: false };
    const { error } = this.schema.validate(this.state.data, options);

    if (!error) return null;

    const errors = {};
    for (const detail of error.details)
      errors[detail.context.key] = detail.message;
    return errors;
  }

  // Validerar inputruta fr inputruta. Extraherar reglerna frn Joi-schemat
  // fr just den rutan. Om inga fel grs inget,
  // annars returneras errormeddelandet
  validateProperty({ name, value }) {
    const subSchema = this.schema.extract(name);
    const { error } = subSchema.validate(value);

    if (!error) return null;

    return error.message;
  }

  // Kollar Inputrutan och fr varje knapptryckning valideras det mot
  // validateProperty(). Felen sparas i state.
  handleChange = ({ target: input }) => {
    const errors = { ...this.state.errors };
    const errorMessage = this.validateProperty(input);
    if (errorMessage) errors[input.name] = errorMessage;
    else delete errors[input.name];

    const data = { ...this.state.data };
    data[input.name] = input.value;
    this.setState({ data, errors });
  };

  // Frhindrar defaultfldet, att ladda om sidan helt.
  // Om validate() hittar fel s sparas dessa i errors. Statet uppdateras med
  //errors eller ett tomt objekt d det inte fr vara null eller undefined.
  handleSubmit = (e) => {
    e.preventDefault();

    const errors = this.validate();
    this.setState({ errors: errors || {} });
    if (errors) return;

    this.doSubmit();
  };

  // Hjlpfunktion s du enkelt kan rendera submit-knapp i formulret.
  // Knappen r avaktiverad nr validate r sant.
  renderButton(label) {
    return (
      <button className="btn btn-primary" disabled={this.validate()}>
        {label}
      </button>
    );
  }

  // Hjlpfunktion s du enkelt kan rendera Input-rutorna i formulret.
  renderInput(name, label) {
    const { data, errors } = this.state;
    return (
      <Input
        name={name}
        label={label}
        value={data[name]}
        error={errors[name]}
        onChange={this.handleChange}
      />
    );
  }
}

export default Form;
